<template>
  <div id="app-base-socket-ipc">
    <div class="one-block-1">
      <span>
        1. 
      </span>
    </div>  
    <div class="one-block-2">
      <a-space>
        <a-button @click="handleInvoke"> - </a-button>
        {{ message1 }}
      </a-space>
      <p></p>
      <a-space>
        <a-button @click="handleInvoke2"> - async/await</a-button>
        {{ message2 }}
      </a-space>            
    </div>   
    <div class="one-block-1">
      <span>
        <!--  -->
        2. 
      </span>
    </div>  
    <div class="one-block-2">
      <a-space>
        <a-button @click="handleSendSync"></a-button>
        {{ message3 }}
      </a-space>   
    </div>        
    <div class="one-block-1">
      <span>
        3.  
      </span>
    </div>  
    <div class="one-block-2">
      <a-space>
        <a-button @click="sendMsgStart"></a-button>
        <a-button @click="sendMsgStop"></a-button>
        {{ messageString }}
      </a-space>
    </div>  
  </div>
</template>
<script>
import { ipcApiRoute } from '@/api/main'
export default {
  data() {
    return {
      messageString: '',
      message1: '',
      message2: '',
      message3: ''
    }
  },
  mounted () {
    this.init();
  },
  methods: {
    init () {
      const self = this;
      //  on 
      this.$ipc.removeAllListeners(ipcApiRoute.ipcSendMsg);
      this.$ipc.on(ipcApiRoute.ipcSendMsg, (event, result) => {
        console.log('[ipcRenderer] [socketMsgStart] result:', result);

        self.messageString = result;
        // 
        event.sender.send(ipcApiRoute.hello, 'electron-egg');
      })
    },
    sendMsgStart() {
      const params = {
        type: 'start',
        content: ''
      }
      this.$ipc.send(ipcApiRoute.ipcSendMsg, params)
    },
    sendMsgStop() {
      const params = {
        type: 'end',
        content: ''
      }
      this.$ipc.send(ipcApiRoute.ipcSendMsg, params)
    },
    handleInvoke () {
      const self = this;
      this.$ipcInvoke(ipcApiRoute.ipcInvokeMsg, '-').then(r => {
        console.log('r:', r);
        self.message1 = r;
      });
    },
    async handleInvoke2 () {
      const msg = await this.$ipcInvoke(ipcApiRoute.ipcInvokeMsg, '');
      console.log('msg:', msg);
      this.message2 = msg;
    },
    handleSendSync () {
      const msg = this.$ipcSendSync(ipcApiRoute.ipcSendSyncMsg, '');
      this.message3 = msg;
    },
  }
}
</script>
<style lang="less" scoped>
#app-base-socket-ipc {
  padding: 0px 10px;
  text-align: left;
  width: 100%;
  .one-block-1 {
    font-size: 16px;
    padding-top: 10px;
  }
  .one-block-2 {
    padding-top: 10px;
  }
}
</style>
